@import url('Global.css');

#menu-container {
    position: fixed;
    bottom: 0;
    padding: 0.3rem 0.5rem;
    width: calc(100% - 1rem);
    height: 5.2rem;
    z-index: 3;
    display: flex;
    flex-direction: row;
    align-items: center;
}

#menu-detail-container {
    display: flex;
    flex: 1;
    flex-direction: row;
    align-items: center;
    height: 100%;
    overflow-x: hidden;
    margin-right: 0.5rem;
    transition: all .4s linear;
    -webkit-transition: all .4s linear;
    -moz-transition: all .4s linear;
    -ms-transition: all .4s linear;
    -o-transition: all .4s linear;
}
#menu-detail-container.close {
    transform: translate(150%, 0);
    -webkit-transform: translate(150%, 0);
    -moz-transform: translate(150%, 0);
    -ms-transform: translate(150%, 0);
    -o-transform: translate(150%, 0);
    opacity: 0;
}
#menu-detail-container.open {
    transform: translate(0, 0);
    -webkit-transform: translate(0, 0);
    -moz-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
    -o-transform: translate(0, 0);
    opacity: 1;
}

#menu-images-container{
    flex: 1;
    list-style: none;
    display: flex;
    flex-direction: row;
    height: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
}

#menu-images-container .tiny-image-list {
    margin: 0 0.5rem;
    position: relative;
}
#menu-images-container .tiny-image-list .tiny-image {
    object-fit: cover;
    object-position: center;
    box-shadow: 0 0 5.5px #999;
    box-sizing: border-box;
    border: none;
    cursor: pointer;
}
#menu-images-container .tiny-image-list.active .tiny-image {
    border: 2px solid var(--description-green);
    /* box-shadow: 0 0 10px var(--description-green); */
}

#menu-images-container .tiny-image-list .tiny-image.hover {
    box-shadow: 0 0 10px var(--description-green);
    opacity: 0.5;
}

