.addr-notation-container {
    position: relative;
    flex-basis: 0.1rem;
    flex-shrink: 0;
    background-color: var(--description-green);
    height: 100%;
    cursor: pointer;
    transition: flex-basis 0.3s linear;
    -webkit-transition: flex-basis 0.3s linear;
    -moz-transition: flex-basis 0.3s linear;
    -ms-transition: flex-basis 0.3s linear;
    -o-transition: flex-basis 0.3s linear;
}

.tiny-iamge-addr-annotation {
    display: flex;
    color: white;
    width: 5rem;
    height: 5rem;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    font-size: 0.6rem;
    text-align: center;
    align-items: center;
    justify-content: center;
    background-color:rgba(0, 0, 0, 1);
    visibility: visible;
}