@import url("./Global.css");
#image-item-container {
    list-style-type: none;
}

#image-item-container .image {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity .6s linear;
  -webkit-transition: opacity .6s linear;
  -moz-transition: opacity .6s linear;
  -ms-transition: opacity .6s linear;
  -o-transition: opacity .6s linear;
}
#first-image-container {
  display: flex;
  flex-direction: row;
  padding: 0 !important;
  height: 100% !important;
}

#image-item-container .active {
  visibility: visible;
  opacity: 1;
  width: 100%;
  height: calc(100% - 5.6rem);
  transition: all 0.3s linear;
  -webkit-transition: all 0.4s linear;
  -moz-transition: all 0.3s linear;
  -ms-transition: all 0.3s linear;
  -o-transition: all 0.3s linear;
  will-change: auto;
}
#image-item-container .active.menu-active {
  padding-bottom: 5.6rem;
  padding-top: 0rem;
}
#image-item-container .active.menu-close {
  padding-bottom: 2.8rem;
  padding-top: 2.8rem;
}

#image-item-container .fade {
  opacity: 0;
}

#image-item-container .active img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center center;
}

#first-image {
  object-fit: cover !important;
  object-position: left top !important;
}
#first-image {
  width: 50% !important;
}
@media screen and (max-width: 799px) {
  #first-image {
      display: none;
  }
}