@import url('./Global.css');
#profile-container {
    background-color: var(--blue-white);
    width: 50%;
    box-sizing: content-box;
}
#profile-photo {
    display: none;
}

#basic-profile-container {
    padding-top: 1rem;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    margin: 0px 0px 65px calc((100% - 370px) * 0.5);
    box-sizing: content-box;
}

#name {
    font: normal normal normal 4.5rem CormorantGaramond-bold;
    letter-spacing: 0.02rem;
}
#occupation {
    font: normal normal normal 1.6rem Montserrat-regular;
    padding: 1rem 0 1rem 0.35rem;
}

#contact-container {
    list-style: none;
    font: normal normal normal 0.9rem Montserrat-regular;
    padding-left: 0.35rem;
    margin-top: 5rem;
}
#contact-container strong {
    padding: 0 0.5rem 0 0;
    font: normal normal normal 0.9rem Montserrat-bold;
}
#contact-container .contact-class {
    padding: 0.6rem 0;
}
#contact-container .contact-class a {
    color: var(--blue);
}
@media screen and (max-width: 799px) {
    #profile-container {
        width: 100%;
    }

    #profile-photo {
        display: block;
        z-index: -1;
        width: 10rem;
        height: 10rem;
        border-radius: 5rem;
        -webkit-border-radius: 5rem;
        -moz-border-radius: 5rem;
        -ms-border-radius: 5rem;
        -o-border-radius: 5rem;
        overflow: hidden;
        position: absolute;
        top: -5rem;
        transform: translateX(-50%);
}
    #profile-photo img{
        object-fit: contain;
        object-position: center center;
    }
}
