    cursor: default;
}
#welcome-name.show #name-container:hover{
    color: var(--description-green);
}

#profile-container {
    padding: 1rem;
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    transition: all .3s ease;
    -webkit-transition: all .3s ease;
    -moz-transition: all .3s ease;
    -ms-transition: all .3s ease;
    -o-transition: all .3s ease;
    will-change: auto;
    color: black;
}

#profile-container.show {
    visibility: visible;
    opacity: 1;
    width: 100%;
    height: 100%;
}
#profile-container h5 {
    line-height: 2rem;
    padding: 1rem 0;
    font-size: 1rem;
    font-weight: normal;
    border-top: 2px solid;
    border-image-source: linear-gradient(to left, transparent, transparent, var(--description-green), transparent ,transparent);
    border-image-slice: 1;
}
#profile-container ul {
    list-style: none;
}
.basic-info {
    padding: .5rem 0;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.basic-info:before {
    content: "";
    margin-right: 1rem;
    display: inline-block;
    width: 1.5rem;
    height: 1.5rem;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}
#email.basic-info::before {
    background-image: url("../images/email.svg");
}
#location.basic-info::before {
    background-image: url("../images/location.svg");
}

.other-link {
