@import url("Global.css");

#image-description-container {
    background-image: url("../images/info.svg");
    background-repeat: no-repeat;
    background-position: center left;
    background-size: contain;
    height: 1.5rem;
    width: 1.5rem;
    position: fixed;
    top: 1rem;
    left: 1rem;
    z-index: 3;
    cursor:pointer;
}
#image-description-container.hide {
    display: none;
}
#image-description-container.show {
    display: block;
}

#image-description {
    position: absolute;
    left: 1.7rem;
    padding: 0;
    margin: 0;
    list-style: none;
    width: max-content;
    display: flex;
    flex-direction: row;
    height: 100%;
    align-items: center;
    font-size: 0.8rem;
    color: var(--description-green);
    will-change: auto;
    transition: all .3s linear;
    -webkit-transition: all .3s linear;
    -moz-transition: all .3s linear;
    -ms-transition: all .3s linear;
    -o-transition: all .3s linear;
    z-index: 1;
}
#image-description.hide {
    opacity: 0;
    top: 1rem;
}
#image-description.show {
    opacity: 1;
    top: 0;
}

#image-description li {
    padding: 0.1rem;
    padding-right: 0.5rem;
}